import { _decorator, Component, Prefab } from "cc";
import { RoleHeadItem } from "../base/obj/role/ui/RoleHeadItem";

const { ccclass, property } = _decorator;

@ccclass('FightTeam')
export class FightTeam extends Component {
    heads: RoleHeadItem[] = [];
    protected onLoad(): void {
        for (let index = 0; index < this.node.children.length; index++) {
            const v = this.node.children[index];
            v.active = false
            this.heads.push(v.getComponent(RoleHeadItem));
        }
    }

    getFree() {
        for (let index = 0; index < this.heads.length; index++) {
            const v = this.heads[index];
            if (v.roleInfo == null) {
                return v
            }
        }
        return null
    }

}